

















import Vue from "vue";


export default Vue.extend({
    props: ["mensaje", "snackbar"],
    data() {
        return {
            mostrar: false
        };
    },
    methods: {
        cambiarEstadoSnackbar() {
            this.$emit("alertfin");
        }
    },
    watch: {
        snackbar(nuevoValor) {
            this.mostrar = nuevoValor;
        }
    }
});
